<template>
  <div id="app">
    <div id="sound"></div>
    <div class="info" v-if="connected === false">
      <div class="form-group mx-sm-3 mb-2 row">
        <input type="text" class="form-control col-md-10" required placeholder="name" v-model="name" :class="{'danger' : name_used}">
        <button type="submit" class="btn btn-primary mb-2" @click.prevent="connect">Confirm</button>
        <span v-show="name_used">Username already used</span>
      </div>
    </div>
    <div class="container" v-else>

      <Modal @doWihsper="doWihsper" v-if="wantWhisper" @close="closeModal" :reception="onlyFor"></Modal>

      <div class="row">
        <OnlineUsers :users="users_online" @WhisperTo="WhisperTo"></OnlineUsers>
        <ChatBox :name="name" :io="io"></ChatBox>
      </div>
    </div>
  </div>
</template>

<script>

import socketIO from 'socket.io-client';
import Modal from './components/Modal'
import OnlineUsers from './components/OnlineList'
import ChatBox from './components/ChatBox'
import {EventBus} from './components/helpers/bus'

export default {
    components:{
        Modal,
        OnlineUsers,
        ChatBox
    },
    data(){
        return{
            name: '',
            connected: false,
            notifications: [],
            users_online: [],
            name_used: false,
            wantWhisper: false,
            onlyFor: '',
            io : socketIO( process.env.VUE_APP_SOCKET_URL),
        }
    },
    created(){
        window.onbeforeunload = () => {
            this.io.emit('leave', this.name)
        }

        this.io.on('connected', (data) => {

            this.notifications.push({
                name : data.name,
                action: 'joined'
            })

            this.cleanNotifications()

        })

        this.io.on('connected_users', (users) => {
            this.users_online = users.filter(name => {
                return name !== this.name
            });
        })

        this.io.on('user_left', (name) => {
            this.users_online = this.users_online.filter(item => !(item === name))

            this.notifications.push({
                name,
                action: 'left'
            })

            this.cleanNotifications()
        })
        this.io.on('connect_response', (response) => {
            this.name_used = !response;
            this.connected = response;
        })
    },
    methods: {
        closeModal(){
            this.wantWhisper = false;
            this.onlyFor = '';
        },
        doWihsper(message){

            EventBus.$emit('new_message_sent', {
                content : message.content,
                sent : 1,
                name: this.name,
                secret: true,
                onlyFor: message.onlyFor,
            })
        },
        WhisperTo(name){
            this.wantWhisper = true,
            this.onlyFor = name
        },
        cleanNotifications(){
            setTimeout(() => {
                this.notifications = []
            }, 3000)
        },
        connect(){
            if(this.name !== '' && !this.users_online.includes(this.name)){
                this.connected = true
                this.io.emit('connected',this.name)
            }
        }
    }
}
</script>

