<template>
  <div class="modal show-modal" id="secret-modal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Write some secret message to {{ reception }}</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close" @click.prevent="cancelWhisper">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <textarea class="form-control send-message" rows="3" placeholder="Write a reply..." v-model="wihsper_content"></textarea>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-dismiss="modal" @click.prevent="cancelWhisper">Close</button>
          <button type="button" class="btn btn-primary" @click.prevent="doWihsper">Send</button>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
export default {
    name: 'modal',
    props:[
        'reception'
    ],
    data(){
        return{
            wihsper_content: '',
        }
    },
    methods:{
        doWihsper(){
            if(this.reception !== '' && this.wihsper_content !== ''){
                this.$emit('doWihsper', {
                    content : this.wihsper_content,
                    secret: true,
                    onlyFor: this.reception,
                });
                this.cancelWhisper();
            }
        },
      cancelWhisper(){
          this.wihsper_content = '';
          this.$emit('close')
      },
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .show-modal{
    display: block;
  }
</style>